@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#home.root {
    background: #E7D1B7;
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-template-rows: repeat(12, 1fr);
}

#home.title {
    grid-row: 3/5;
    grid-column: 3/5;
    display: flex;
    align-items: center;
    justify-content: center;
}

.image img {
    width: 356px;
    height: 475px;
    border: solid #F4F2E8;
    border-width: 10px;
    border-radius: 50%;
}

.image {
    grid-column: 3/5;
    grid-row: 4/10;
    display: flex;
    align-items: center;
    justify-content: center;
}

.about {
    grid-row: 5/8;
    grid-column: 6/8;
    color: white;
    font-family: 'Playfair Display', serif;
    font-size: 24px;
    color: #594734;
}

.links {
    grid-row: 8/9;
    grid-column: 6/8;
    display: flex;
    align-items: top;
    justify-content: space-evenly;
}

.menu {
    grid-row: 4/5;
    grid-column: 7/8;
    display: flex;
    justify-content: flex-end;
}   
#portfolio.root {
    background: #B6A1A2;
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-template-rows: repeat(55, 50px);
    height: 100%;
}

#portfolio.title {
    font-family: 'Abril Fatface', cursive;
    color: #303C36;
    font-size: 64px;
    grid-column: 4/7;
    grid-row: 2/3;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hci {
    background-color: #F4F2E8;
    grid-column: 2/9;
    grid-row: 4/53;
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: repeat(52, 50px);
    border-radius: 10px;
    font-family: 'Playfair Display', serif;
}

.navigation {
    grid-row: 1/2;
    grid-column: 7/-1;
    display: flex;
    align-self: flex-end;
}

.hciTitle {
    font-size: 36px;
    font-family: 'Abril Fatface', cursive;
    color: "#303C36";
    text-align: center;
    grid-row: 2/3;
    grid-column: 2/7;
    color: #5F7399;
}

.descriptionTitle {
    grid-row: 4/5;
    grid-column: 2/7;
    text-align: center;
    font-family: 'Abril Fatface', cursive;
    color: #5F7399;
    font-size: 24px;
}

.descriptionContent {
    grid-row: 5/6;
    grid-column: 2/7;
}

.conceptTitle {
    grid-row: 7/8;
    grid-column: 2/7;
    text-align: center;
    font-family: 'Abril Fatface', cursive;
    color: #5F7399;
    font-size: 24px;
}

.conceptGallery {
    grid-row: 8/20;
    grid-column: 2/7;
}

.refinedTitle {
    grid-row: 21/22;
    grid-column: 2/7;
    text-align: center;
    font-family: 'Abril Fatface', cursive;
    color: #5F7399;
    font-size: 24px;
}

.refinedGallery {
    grid-row: 22/34;
    grid-column: 2/7;
} 

.videoTitle {
    grid-row: 35/36;
    grid-column: 2/7;
    text-align: center;
    font-family: 'Abril Fatface', cursive;
    color: #5F7399;
    font-size: 24px;
}

.video {
    grid-row: 36/43;
    grid-column: 2/7;
    display: flex;
    justify-content: center;
    align-items: center;
}

.extras {
    grid-row: 44/45;
    grid-column: 2/7;
    text-align: center;
    font-family: 'Abril Fatface', cursive;
    color: #5F7399;
    font-size: 24px;
    display: flex;
    justify-content: space-evenly;
}

.myContributionsTitle {
    grid-row: 46/47;
    grid-column: 2/7;
    text-align: center;
    font-family: 'Abril Fatface', cursive;
    color: #5F7399;
    font-size: 24px;
}

.myContributions {
    grid-row: 47/49;
    grid-column: 2/7;
}
